<script>
  export default {
    name: "ContactUs",
    data(){
      return {
        wechatShow:false,
        mailShow:false
      }
    },
    methods:{
      enter(val){
        if(val=="1"){
          this.wechatShow=true
        }else {
          this.mailShow=true
        }
      },
      leave(val){
        if(val=="1"){
          this.wechatShow=false
        }else {
          this.mailShow=false
        }
