    <div id="app">
        <div id="container1" >
            <Page1Head @result="getresult" @labels="getlabels"/>
        </div>
        <div id="container2" >
            <img src="@/assets/u39.png" width="100%"/>
        </div>
        <div id="labels">
            <WordsLabel :labels="labels"/>
        </div>
        <!--        显示部分-->
        <div id="container3">
            <!--                遮盖-->
            <!--                <div id="shade" v-if="!flag">-->
            <!--                    <img src="@/assets/u68.svg" width="8%" id="push" @click="flag=true"/>-->
            <!--                </div>-->
            <resultWords :text="result"/>
        </div>

        <!--        尾部-->
<!--        <div id="container4" v-bind:style="{width:screenWidth+'px',height:(screenWidth/2560)*818+'px'}">-->
<!--            <ContactUs/>-->
<!--            <div id="statement">点子·创意广告词灵感站 版权所有</div>-->
<!--        </div>-->
        <div id="container4">
            <fixedBottom/>
        </div>
    </div>


</template>

<script>
    // import ContactUs from "../../../components/ContactUs";
    import Page1Head from "../components/Page1Head";
    import WordsLabel from "../components/WordsLabel";
    import resultWords from "../components/resultWords";
    import fixedBottom from "../../../components/fixedBottom";
    export default {
        components:{
            Page1Head,
            // ContactUs,
            WordsLabel,
            resultWords,
            fixedBottom
        },

        data() {
            return {
                screenWidth: document.documentElement.clientWidth,
                // screenHeight: document.documentElement.clientHeight,
                //交没交钱
                flag:false,
                //搜索标签
                labels:[],
                select:[],
                search:{},
                result:{
                    "flag":false
                },
            }
        },
        beforeCreate() {
            var that=this
            if(that.screenWidth<780){
                that.screenWidth=780
                // console.log(that.screenWidth)
            }
        },

        //渲染成html前
        created: function () {

        },
        // 渲染成html后
        mounted() {
            // 屏幕自适应
            const that = this
            window.onresize = () => {
                return (() => {
                    window.screenWidth = document.documentElement.clientWidth
                    if(window.screenWidth>=780){
                        that.screenWidth = window.screenWidth

                    }

                })()
            }
            var css = `
        html{
        font-size: ${that.screenWidth/80}px;
        }
        `
            document.write(`<style>${css}</style>`)

        },
        // 页面监听函数
        watch: {
            // 屏幕自适应
            screenWidth(val){
                // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
                if(!this.timer){
                    // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
                    this.screenWidth = val
                    //
                    // this.$router.go(0)
                    this.timer = true
                    let that = this
                    setTimeout(function(){
                        // console.log(that.screenWidth)
                        that.timer = false
                    },400)
                }
            },


        },
        methods:{
            getlabels:function(e){
              this.labels=e
            },
            getresult:function (e) {
                this.result=e
                // console.log(e)
            }

        }

    }
</script>

<style lang="less">
