        /*color: #2c3e50;*/
        /*overflow-x: hidden;*/
    }

    #container1{
        position: fixed;
        top: 0;
        width: 100%;
        background-color: white;
        box-shadow: 0 5px 15px -2px darkgrey;
        z-index: 100;

    }

    #container3{
        width: 100%;
    }

    #container4{
        width: 100%;
        height: 100%;
        background: url('~@/assets/u24.png') no-repeat;
        background-size: 100% 100%;
        background-position: center;
        position: relative;
    }

    /*#words-show{*/
    /*    !*width: 30%;*!*/
    /*    width: 20rem;*/
    /*    height: 30rem;*/
    /*    position: relative;*/
    /*}*/
    #shade{
        width: 20rem;
        height: 40rem;
        position: absolute;
        top: 100px;
        /*background-color: grey;*/
        background-image:
                linear-gradient(
                to bottom,
                rgba(255,255,255,0) 0%,
                rgba(255,255,255,0.3) 5%,
                rgba(255,255,255,0.5) 10%,
                rgba(255,255,255,0.8) 14%,
                rgba(255,255,255,1) 16%);
    }
    #push{
        margin:5rem 0 0 7rem;
    }


    #statement{
        text-align:center;
        color: #A9A9A9;
        font-size: 0.8125rem;
        position: absolute;
        bottom: 8px;
        left: 0;
        right: 0;
        margin: auto;
    }
</style>