<template>
    <div id="Page1Head">
        <div id="head-img">
            <img src="~@/assets/u52.png" width="94px" height="40px"/>
        </div>
        <div id="head-input-box">
        <div id="head-input">
            <input v-model="input1" placeholder="请输入关键词" class="input">
            <div id="search-box">
                <!--                选项菜单动画-->
                <img src="@/assets/搜索_u131.svg" width="30px" v-on:click="show = !show" @mouseover="show=true" />
                <!--                整个搜索框-->
                <transition name="slide" id="search-menu">
                    <ul class="list" v-if="show">
                        <!--                    第一个输入框-->
                        <div class="plus">
                            <img src="~@/assets/plus.png" v-on:click="input2show=!input2show" class="plus-img"
                                 @mouseover="input2show=true" @mouseout="input2show=false" />
                            <transition name="moveR">
                                <div  v-if="input2show" class="input-text"  @mouseover="input2show=true">
                                    <input v-model="input2" placeholder="请输入关键词" class="input">
                                </div>
                            </transition>
                        </div>
                        <!--                        第二个输入框-->
                        <div class="plus">
                            <img src="~@/assets/plus.png" v-on:click="input3show=!input3show" class="plus-img"
                                 @mouseover="input3show=true" @mouseout="input3show=false"/>
                            <transition name="moveR">
                                <div  v-if="input3show" class="input-text" @mouseover="input3show=true">
                                    <input v-model="input3" placeholder="请输入关键词" class="input">
                                </div>
                            </transition>
                        </div>
                        <!--                        第三个选择框-->
                        <div class="plus">
                                <div class="list-select" v-on:click="select1show=!select1show" @mouseenter="select1show=true" @mouseleave="select1show=false">
                                <p class="select-text">选</p>
                            </div>
                            <transition name="moveR">
<!--                                    <div  v-if="select1show" class="input-text-select" @mouseenter="select1show=true" @mouseleave="select1show=false">-->
                                <div  v-if="select1show" class="input-text-select" @mouseenter="select1show=true">
                                        <div v-for="(item,index) in select1words" :key="index" class="select-button-select" @click="clickSelect(index)" :class="{'select-judge':select1flag.indexOf(index)>-1}">{{item}}</div>
                                </div>
                            </transition>
                        </div>
                        <!--                        第四个选择框-->
                        <div class="plus">
                            <div class="list-select" v-on:click="select2show=!select2show" @mouseenter="select2show=true" @mouseleave="select2show=false">
                                <p class="select-text">{{option1}}</p>
                            </div>
                            <transition name="moveR">
<!--                                <div  v-if="select2show" class="input-text" @mouseenter="select2show=true" @mouseleave="select2show=false">-->
                                <div  v-if="select2show" class="input-text" @mouseenter="select2show=true">
                                    <button class="select-button" ref="option1" :style="{'background-color':option1color}" v-on:click="option(0)">同音</button>
                                    <button class="select-button" ref="option2" :style="{'background-color':option2color}" v-on:click="option(1)">同字</button>
                                </div>
                            </transition>
                        </div>
                        <!--                        开始按钮-->
                        <div class="plus">
                            <scroll-to target="#labels">
                                <img src="~@/assets/开始_u115.svg" v-on:click="startQuery" width="40px"/>
                            </scroll-to>
                        </div>

                    </ul>
                </transition>

            </div>

        </div>
        </div>
        <div id="head-back">
            <a href="/" id="back">返回首页</a>
        </div>

    </div>
</template>

<script>
    import data from "../../../mock/mock.js"
    export default {
        name: "Page1Head",
        data(){
            return{
                show: false,
                input1:"",
                input2:"",
                input3:"",
                input2show:false,
                input3show:false,
                //select1flag:[true,true],
                select1flag:[0,1,2,3],
                select1words:['成语','诗词','流行语','歇后语'],
                option1:"同音",
                select1show:false,
                select2show:false,
                option1color:"rgb(255, 210, 86)",
                option2color:"rgb(220, 220, 220)",
                search_select1:['成语','诗词'],
                search:{},
                result:{}


            }
        },
        methods: {
            clickSelect:function(e){
                var index=this.select1flag.indexOf(e)
                //若包含该元素
                if(index > -1){
                    this.select1flag.splice(index,1)
                }
                //若不包含该元素
                else{
                    this.select1flag.push(e)
                }
            },

            option:function(e){
                if (e == 0) {
                    if (this.$refs.option1.style.backgroundColor == "rgb(255, 210, 86)") {
                        // this.$refs.choose1.style.backgroundColor = "rgb(220,220,220)"
                        this.option1color = "rgb(220,220,220)"
                        this.option2color = "rgb(255, 210, 86)"
                        this.option1="同字"
                    } else {
                        // this.$refs.choose1.style.backgroundColor = "rgb(255, 210, 86)"
                        this.option2color = "rgb(220,220,220)"
                        this.option1color = "rgb(255, 210, 86)"
                        this.option1="同音"
                    }
                } else if (e == 1) {
                    if (this.$refs.option2.style.backgroundColor == "rgb(255, 210, 86)") {
                        this.option2color = "rgb(220,220,220)"
                        this.option1color = "rgb(255, 210, 86)"
                        this.option1="同音"

                    } else {
                        this.option1color = "rgb(220,220,220)"
                        this.option2color = "rgb(255, 210, 86)"
                        this.option1="同字"
                    }
                }
            },

            startQuery: function () {
                this.show=false
                var that = this
                var labels = []
                var source=[]

                //添加labels
                if (that.input1 != "") {
                    labels.push(that.input1)
                }
                if (that.input2 != "") {
                    labels.push(that.input2)
                }
                if (that.input3 != "") {
                    labels.push(that.input3)
                }
                //添加source
                for(var i in this.select1flag){
                    let k=this.select1flag[i]
                    source.push(this.select1words[k])
                }

                if (source.length==0) {
                    alert("请至少选择一种库")
                } else if (labels.length == 0) {
                    alert("请至少输入一个关键词")
                } else {
                    //为search添加数据
                    that.search["keyword"] = labels
                    that.search["source"] = source
                    that.search["option"] = [this.option1]
                    that.$emit('labels', labels)
                    //axios发送post请求
                    that.$axios.post('dianzi/py', that.search)
                        .then(function (res) {
                            that.result = res.data
                            that.$emit('result', that.result)
                        })
                        .catch(function (error) {
                        })

                    //测试mock用
                    // that.$axios.post('http://test.cn', that.search)
                    //     .then(function (res) {
                    //         that.result = res.data
                    //         that.$emit('result', that.result)
                    //         // that.$set(that.result, res.data)
                    //     })
                    //     .catch(function (error) {
                    //     })

                }
            }
        }
    }


</script>

<style scoped>
    #Page1Head{
        height: 100%;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        background-color: white;
        justify-content:space-between;
        z-index: 100;
    }
    #head-img{
        margin: 0.65rem 0 3px 2.75rem;
    }
    #head-back{
        margin: 20px 3rem auto 0;
    }
    #head-input-box{
        display: flex;
        justify-content: flex-end;
        width: 50%;
    }
    #head-input{
        height: 45px;
        width: 180px;
        margin: auto 0 auto 0;
        /*background: #1a6de3;*/
        background-color: #1a6de3;
        /*outline: none;*/
        border-style: none;
        border-radius: 25px;
        text-align: center;
        display: flex;
    }
    .input{
        margin: auto 0;
        width: 145px;
        background-color: transparent;
        padding: 0 20px;
        color: #fefefe;
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        border-style: none;
        outline: none;
    }
    #search-box{
        margin-top: 8px;
        position: relative;
    }
    #search-menu{
        top: 0;
        position: absolute;
        background-color: white;
        width: 30px;
        height: 50px;
    }

    .list {
        border-radius: 0 0 25px 25px;
        background-color: white;
        position: absolute;
        left: -10px;
        width: 45px;
        margin-top:5px;
        padding-top: 15px;
        list-style-type: none;
        transform-origin: top;
        transition: transform .4s ease-in-out;
        /*overflow: hidden;*/
    }
    .plus{
        margin-bottom: 10px;
        position: relative;
    }
    .plus-img{
        width: 35px;
        height: 35px;
        position: relative;
        z-index: 1;
    }
    .input-text{
        display: flex;
        color: white;
        width: 170px;
        height: 35px;
        text-align: center;
        border-radius: 25px;
        background-color: #1a6de3;
        position: absolute;
        left: -135px;
        top: 0;
        z-index: 0;
        display: flex;
    }

    .input-text-select{
        color: white;
        width: 220px;
        height: 35px;
        text-align: center;
        border-radius: 25px;
        background-color: #1a6de3;
        position: absolute;
        left: -185px;
        top: 0;
        z-index: 0;
        display: flex;
    }

    input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
        /* WebKit browsers */
        color: #fefefe;

    }
    .list-select{
        margin: auto;
        font-size: 17px;
        background-color: #1a6de3;
        border-radius: 50px;
        width: 35px;
        height: 35px;
        color: white;
        text-align: center;
        position: relative;
        z-index: 1;
    }
    .select-button{
        font-size: 10px;
        margin: 3px 0 0 30px;
        /*background-color: #ffd256;*/
        border-radius: 50px;
        width: 30px;
        height: 30px;
        color: white;
        justify-content:space-around;
        align-items:center;
        text-align: center;

        /*z-index: 1;*/
        border:none
    }
    .select-button-select{
        font-size: 8px;
        display: flex;
        vertical-align: center;
        margin: 3px 0 0 15px;
        background-color: rgb(220, 220, 220);
        border-radius: 50px;
        width: 30px;
        height: 30px;
        color: white;
        justify-content:space-around;
        align-items:center;
        text-align: center;

        /*z-index: 1;*/
        border:none
    }
    .select-judge{
        background-color: #ffd256;
    }

    .select-text{
        width: 100%;
        height: 100%;
        font-size: 12px;
        padding-top: 8px;

    }
    #back{
        font-size: 1rem;
        color: #65A3FF;
        text-decoration: none;
    }
    #back:hover{
        font-size: 1rem;
        color: #DCDCDC;
    }

    .slide-enter{
        transition: all 0.1s linear;
        transform: scaleY(0);
    }
    .slide-leave-to{
        /*transition: all 0.1s linear;*/
        transform: scaleY(0);
    }

    .moveR-enter-active,  .moveR-leave-active {
        transition: all 0.2s linear;
        /*transform: translateX();*/
    }
    .moveR-enter {
        transform: translateX(10%);
        transition: all .15s cubic-bezier(1.0, 0.5, 0.8, 1.0);
        width: 150px;
    }
    .moveR-leave {
        transform: translateX(10%);
        transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
        /*width: 140px;*/
        /*left: -105px;*/
        width: 130px;
        left: -100px;

    }
    .moveR-leave-to{
        transform: translateX(10%);
        width: 130px;
        left: -100px;

    }


</style>